.navbar {
  background: transparent;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar.active {
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
}

