.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
  width: auto;
  justify-content: end;
  margin-right: 2rem;
}

/* .nav-item {
  position: relative;
  top: 0;
} */

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 0.7rem;
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.nav-links-mobile-cus {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 1200px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    position: absolute;
    left: -100%;
    top: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 9999;
  }

  .nav-menu.active {
    background: #000;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 9999;
  }

  .nav-links {
    text-align: center;
    padding: 0.6rem;
    width: 100%;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 10px;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    z-index: 99999;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    text-decoration: none;
    font-size: 0.9rem;
    background-color: transparent;
    color: #fff;
    padding: 10px 30px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile-cus {
    display: block;
    text-align: center;
    margin: 1rem auto;
    border-radius: 4px;
    font-size: 0.9rem;
    color: #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}
