.carousel-block {
  height: 100vh;
  display: flex;
  justify-content: center;
  position: relative;
}
.carousel-block-navigation {
  position: absolute;
  bottom: 130px;
  right: 80px;
  height: 90px;
  z-index: 102;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 768px) {
  .carousel-block-navigation {
    position: relative;
  }
}
.carousel-block-navigation ul {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.carousel-block-navigation li {
  margin: auto;
  padding: 0;
}

.carousel-direct-slide-link button {
  border: 0;
  width: 28px;
  margin: 0 2px;
  font-size: 10px;
  color: #ffffff;
  height: 3px;
  background-color: #979797;
  opacity: 1;
  cursor: pointer;
}

.carousel-direct-slide-link button.active {
  background: #28e1a5;
  transform: translateY(-1px);
  height: 4px;
}

.carousel-direct-slide-link:hover button,
.carousel-direct-slide-link:active
  button
  .carousel-direct-slide-link
  button:hover,
.carousel-direct-slide-link button:active {
  background: #28e1a5;
}

.arrow-button {
  background-color: transparent;
  border: none;
  color: #c0c0c0;
  font-size: 2rem;
}

@media (max-width: 768px) {
  .arrow-button {
    display: none;
  }
  .carousel-block-navigation {
    right: auto;
    bottom: 26px;
  }
}

.arrow-button:hover,
.arrow-button:active {
  color: #28e1a5;
}

li.video-control-block {
  width: 100%;
  text-align: center;
  margin-bottom: 1rem;
}

.video-control-button {
  display: block;
  background-color: transparent;
  border: 2px solid #c0c0c0;
  color: #c0c0c0;
  margin: 0 auto;
  border-radius: 4px;
  font-size: 1rem;
  padding: 5px 10px;
  cursor: pointer;
}

.video-control-button:hover,
.video-control-button:active {
  border: 2px solid #28e1a5;
  color: #28e1a5;
}
