#social_icon.social-header {
  height: max-content;
  grid-auto-flow: column;
  grid-gap: 16px;
}
.ul_container {
  grid-gap: 16px;
  align-content: center;
}
.ul_container li {
  text-align: center;
}
.ul_container li #link_style {
  padding: 24px 10px 24px;
  display: inline-block;
}
#social_icon.social-nav {
  grid-gap: 32px;
  justify-content: center;
  grid-template-columns: repeat(4, 50px);
}
#social_icon.social-footer {
  grid-auto-flow: column;
  grid-gap: 16px;
}

.container_card_collection {
  grid-template-columns: repeat(auto-fill, minmax(0, 243px));
  grid-gap: 32px;
}

.investor_logo_container {
  grid-template-columns: repeat(auto-fill, minmax(0, 243px));
  place-items: center;
  grid-gap: 48px;
}
.investor_logo_container .backed {
  max-height: 160px;
}

@media screen and (min-width: 1028px) {
  .ul_container {
    grid-auto-flow: column;
  }
}
@media screen and (max-width: 1024px) {
  .ul_container {
    grid-gap: 54px;
  }
}
@media screen and (max-width: 428px) {
  .ul_container {
    grid-gap: 16px;
  }
  #social_icon.social-nav {
    grid-template-columns: repeat(4, 32px);
  }
}
