.bg-team {
  background: url("../../assets/images/Team/bgTeam.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}

.button__bar {
  display: inline-block;
  vertical-align: middle;
  padding-top: 50px;
  border-radius: 40px;
  text-align: center;
}

.button__bar li {
  list-style: none;
  cursor: pointer;
  display: inline-block;
  margin: 0 3px;
  padding: 0;
}

.button__bar li button {
  border: none;
  background: rgb(124, 124, 124);
  color: transparent;
  cursor: pointer;
  display: block;
  height: 4px;
  width: 30px;
}

.button__bar li.slick-active button {
  background-color: #ddd;
}

.button__bar li button:hover,
.button__bar li button:focus {
  background: #fff;
  outline: 0;
}

.slick-slide img.img-teams {
  border: 5px solid #fff;
  display: block;
  margin: auto;
}
