@import "../../../index.css";

.slide-block {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  opacity: 0;
  transition-duration: 1s ease;
}
.slide-block.active {
  opacity: 1;
  transition-duration: 1s;
}
.slide-block-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}
.slide-block-background-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}

.slide-block-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-image: linear-gradient(transparent, #000c28);
}
.slide-block-text {
  color: #ffffff;
  z-index: 101;
  opacity: 0;
  transition: all 0.8s ease;
}
.slide-block-text:hover {
  opacity: 1;
}
.slide-block-text-cta {
  border: none;
  box-shadow: none;
  display: block;
  background-color: #72777c;
  border-radius: 4px;
  color: #ffffff;
  font-weight: 700;
  margin: 1.5rem auto 0;
  min-height: 48px;
  padding: 0 40px;
  font-size: 0.875rem;
  line-height: 44px;
  transition: background-color 200ms ease;
  text-align: center;
  text-transform: uppercase;
  width: -moz-fit-content;
  width: fit-content;
}
.slide-block-text-cta:hover,
.slide-block-text-cta:active,
.slide-block-text-cta:focus {
  background-color: rgb(0, 73, 147);
}

@media (max-width: 992px) {
  .slide-block-text {
    opacity: 1;
  }
}

@media screen and (min-width: 61.25rem) {
  .slide-block {
    justify-content: center;
    align-items: center;
  }
  .slide-block-text {
    text-align: left;
    margin: 0 0 150px 100px;
  }
  .slide-block-text-cta {
    margin: 1.5rem 0 0;
  }
}
