.slide img {
  width: 100%;
}

.slide {
  transform: scale(0.7);
  transition: transform 300ms;
  opacity: 0.3;
}

.activeSlide {
  transform: scale(1.1);
  opacity: 1;
}

.arrow {
  position: absolute;
  cursor: pointer;
  z-index: 10;
  font-size: 2.4rem;
}

.arrow svg {
  transition: color 300ms;
}

.next {
  right: 0;
  top: -4.5rem;
}

.prev {
  right: 60px;
  top: -4.5rem;
}

.buttonProject {
  bottom: 10px;
  left: 70px;
  padding: 4px 10px;
  font-size: 12px;
}

@media (max-width: 1349px) {
  .buttonProject {
    bottom: -10px;
    left: 55px;
  }
}
@media (max-width: 1023px) {
  .buttonProject {
    bottom: -15px;
    left: 42px;
  }
}
@media (max-width: 810px) {
  .buttonProject {
    bottom: 10px;
    left: 90px;
  }
}
@media (max-width: 767px) {
  .buttonProject {
    bottom: 5px;
    left: 70px;
  }
}
@media (max-width: 639px) {
  .buttonProject {
    bottom: 0px;
    left: 70px;
  }
}
@media (max-width: 490px) {
  .buttonProject {
    bottom: -20px;
    left: 50px;
  }
}
@media (max-width: 410px) {
  .buttonProject {
    padding: 0px 10px;
    font-size: 12px;
  }
}
@media (max-width: 360px) {
  .buttonProject {
    bottom: -20px;
    left: 40px;
    padding: 0px 6px;
    font-size: 8px;
  }
}
