#container_card_project {
  height: 450px;
  transition: all 500ms ease-in-out;
  z-index: 0;
}
@media screen and (max-width: 768px) {
  #container_card_project {
    height: 300px;
  }
}

#container_card_project:hover {
  height: 90%;
  transform: scale(1.1);
  z-index: 4;
}

#container_card_project .desc_wrap_cardproject p {
  cursor: default;
  height: 0%;
  transform: translateX(-130%);
  transition: all 400ms linear;
}

#container_card_project:hover .desc_wrap_cardproject p {
  height: 60%;
  transition-delay: 300ms;
  transform: translateX(0);
  overflow-y: scroll;
}

.title-card {
  writing-mode: vertical-rl;
  text-orientation: mixed;
}

@media (max-width: 640px) {
  .title-card {
    padding-top: 1rem;
    writing-mode: horizontal-tb;
    text-align: left;
  }
}

#container_card_project .desc_wrap_cardproject p::-webkit-scrollbar {
  display: none;
  width: 3px;
}

/* Track */
#container_card_project .desc_wrap_cardproject p::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.15);
}

/* Handle */
#container_card_project .desc_wrap_cardproject p::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.15);
}

/* Handle on hover */
#container_card_project
  .desc_wrap_cardproject
  p::-webkit-scrollbar-thumb:hover {
  background: transparent;
}
