.loader {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 4em);
  width: 6em;
  height: 6em;
  border: 1.1em solid rgba(240, 240, 240, 0.2);
  border-left: 1.1em solid white;
  border-radius: 50%;
  animation: load8 1.1s infinite linear;
  transition: opacity 0.3s;
}

.loader-container::after {
  position: absolute;
  content: 'WAKASPACE';
  top: calc(50% + 2em);
  left: calc(50% - 4em);
  letter-spacing: 0.1em;
  display: grid;
  place-items: center;
  font-size: 32px;
  font-weight: bold;
  color: white;
}

@media (max-width: 820px) {
  .loader,
  .loader-container::after {
    left: calc(55% - 4em);
  }
}

.loader--hide {
  opacity: 0;
}

@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
